import React from "react";
import { SeoContainer } from "@components/Container/seoContainer";
import { graphql } from "gatsby";
import styled from "styled-components";
import Loading from "@components/Loading";

const ResumeLayout = styled.div.withConfig({ componentId: "ResumeLayout" })`
  display: flex;
  justify-content: center;
  align-items: center;
  height: calc(100vh - 70px);
  position: relative;
  iframe {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
  }
`;

const ResumePage = () => (
  <ResumeLayout>
    <Loading />
    <iframe src="https://www.cakeresume.com/dennnny" />
  </ResumeLayout>
);

export default ResumePage;

export const Head = () => (
  <SeoContainer>
    <meta
      name="google-site-verification"
      content="7DkPSaWn_BaOEvNXuBL2Du0jBY_-ZWg1wf52fcGvq8w"
    />
  </SeoContainer>
);

export const query = graphql`
  query ($language: String!) {
    locales: allLocale(filter: { language: { eq: $language } }) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`;
